.p-menu {
  padding: 100px 20px 40px;

  @include media(lg) {
    max-width: 1200px;
    margin: 0 auto;
  }
}

.p-menu__subTitle {
  margin-bottom: $mg_bottom_sm;
}

.p-menu__repletion {
  margin-bottom: $mg_bottom_sm;
  max-width: 320px;
  margin: 0 auto;
}

.p-menu__select-box {
  display: block;
  position: relative;
  width: 60%;
  max-width: 300px;
  margin: 0 auto;

  &::after {
    display: block;
    content: '';
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translateY(calc(-50% + 1px));
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 6px 6px 0 6px;
    border-color: #3aca95 transparent transparent transparent;
  }
}

.p-menu__select {
  width: 100%;
  appearance: none;
  display: block;
  margin: 0 auto 20px;
  padding: 5px 5px 5px 10px;
  background-color: $base_white_light;
  border: 1px solid $base_green;
  border-radius: 20px;
}

// ===========================================================
// MENU__BOX
// ===========================================================
.p-menu__container {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  padding: 40px 0;

  @include media(md) {
    grid-template-columns: 1fr 1fr;
  }
}

.p-menu__item {
  width: 100%;
  display: none;
  box-shadow: 5px 5px 1px 2px $menu_boxshadow_gray;
  border-radius: 14px;
  background-color: $base_white;

  &.is-selected {
    display: block;

    &.is-common {
      display: none;
    }
  }
}

.p-menu__image {
  width: 100%;
  height: auto;
  vertical-align: bottom;
  border-top-left-radius: 14px;
  border-top-right-radius: 14px;
}

.p-menu__box {
  background-color: $base_white;
  width: 100%;
  height: auto;
  border-bottom-left-radius: 14px;
  border-bottom-right-radius: 14px;
  padding: 20px 10px;
  position: relative;

  @include media(lg) {
    padding: 20px;
    min-height: 500px;
    position: relative;
  }
}

.p-menu__title {
  font-weight: bold;
  margin-bottom: $mg_bottom_xs;
  max-width: 90%;
  padding: 0 0 8px 5px;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 7px;
    background-image: repeating-linear-gradient(-45deg, $base_green 0px, #b4a983 1px, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 50%);
    background-size: 7px 7px;
  }
}

.p-menu__text {
  margin-bottom: $mg_bottom_sm;
  @include font-size-rem(14);

  @include media(lg) {
    margin-bottom: 0;
    line-height: 2;
  }
}

// ===========================================================
// PRICE__BOX
// ===========================================================

.p-menu__price-box {
  @include media(lg) {
    width: 90%;
    position: absolute;
    bottom: 20px;
  }

  & p {
    @include font-size-rem(14);
  }

  & .multiple {
    margin-bottom: $mg_bottom_sm;
  }
}

.p-menu__price-num {
  @include font-size-rem(12);
  font-weight: bold;
  display: inline-block;
  border-radius: 4px;
  border: 2px solid $base_green;
  margin-bottom: $mg_bottom_xs;
  min-width: 80px;
  letter-spacing: 4px;
  text-align: center;
}

.p-menu__oneTime-inner {
  display: flex;
  align-items: center;
  @include font-size-rem(14);
}

.p-menu__oneTime-line {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 10px;
  border: none;
  border-top: dotted 2px $base_gray;
  flex: auto;
}

.p-menu__oneTime-price {
  letter-spacing: 2px;
}

.p-menu__firstTime-text {
  position: absolute;
  bottom: 25px;
}

.p-menu__oneTime-careful {
  font-size: inherit;
}

// COURSE
.p-menu__course {
  margin-bottom: $mg_bottom_md;
}

.p-menu__course-pattern {
  &:not(:last-of-type) {
    margin-bottom: $mg_bottom_md;
  }
}

.p-menu__course-title {
  margin-bottom: 5px;
}

.p-menu__course-price {
  display: flex;
  justify-content: center;
  @include font-size-rem(14);
}

.p-menu__course-price-pre,
.p-menu__course-price-after {
  letter-spacing: 2px;
}

.p-menu__course-price-pre {
  display: inline-block;
  position: relative;

  &::before {
    display: block;
    content: '';
    width: 110%;
    height: 2px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: $base_green;
  }
}

.p-menu__course-price-arrow {
  margin: 0 25px;
}

.p-menu__tax {
  text-align: center;
  margin-bottom: $mg_bottom_md;

  @include media(lg) {
    margin-bottom: $mg_bottom_xl;
  }
}

.p-menu__tax-text {
  @include font-size-rem(12);

  @include media(lg) {
    @include font-size-rem(14);
  }
}

.p-menu__btn-wrap {
  margin: 0 20px;
}
